<template>
  <div>
    <form v-if="datosSelected" class="needs-validation" novalidate>
      <div class="form-group row">
        <div class="col-3">
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text">Año</span>
            </div>
            <select class="form-control" v-model="yearSelected">
              <option
                v-for="year in $options.years"
                :key="year"
                :value="year"
                :selected="year == yearSelected"
              >{{ year }}</option>
            </select>
          </div>
        </div>
      </div>
      <Formulario :datosIniciales="datosSelected" @update="actualizar" />
    </form>
  </div>
</template>

<script>
import { postResoluciones, getResoluciones } from "../api/resoluciones";
import Formulario from "../components/resoluciones/Formulario";

const years = [2020, 2019, 2018, 2017, 2016];

export default {
  years,
  components: {
    Formulario
  },
  data() {
    return {
      yearSelected: 2020,
      datos: null
    };
  },
  mounted() {
    this.cargarDatos();
  },
  computed: {
    datosSelected() {
      if (!this.datos) return null;
      return this.datos.find(x => x.year == this.yearSelected);
    }
  },
  methods: {
    mensajeError(texto) {
      console.error(texto);
      return this.$swal({
        title: "Error",
        html: `Ocurrio un error inesperado al realizar la operación.<br/>
        Por favor, si el error persiste contacte al administrador del sistema.`,
        icon: "error",
        allowOutsideClick: false
      });
    },
    mensajeExito() {
      return this.$swal({
        title: "Exito",
        text: "La operación se realizo correctamente.",
        icon: "success",
        allowOutsideClick: false
      });
    },
    cargarDatos() {
      let loader = this.$loading.show();

      getResoluciones()
        .then(data => {
          this.datos = data.resoluciones;
        })
        .catch(error => {
          this.mensajeError(error);
        })
        .finally(() => loader.hide());
    },
    async actualizar(data) {
      const accessToken = await this.$auth.getTokenSilently();

      let loader = this.$loading.show();
      postResoluciones(accessToken, data)
        .then(() => {
          this.mensajeExito();
        })
        .catch(error => {
          this.mensajeError(error);
        })
        .finally(() => loader.hide());
    }
  }
};
</script>

